.App {
  text-align: center;
  height: 100vh;
}

.topSide {
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

.board {
  width: 200px;
  height: 200px;
  background: indianred;
}

.launcher {
  display: flex;
}

.context {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 19px;
}

.machineValue {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 19px;
}

.controlBoard {
  background-color: #bdbdbd;
  height: 50vh;
  /* display: flex;
  flex-direction: column; */
}
.close-btn {
  display: block;
}